/* src/countdown.css */
html, body, #root {
  height: 100%;
  margin: 0;
}

body {
  background-color: darkseagreen;
}

.countdown-container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  text-align: center;
  font-family: Helvetica, sans-serif;
  font-weight: bold;
  /* REMOVE any background-color or background shorthand here */
  background: transparent;
}
.back-btn {
  position: absolute;
  /* Move it lower, adjust 56px as needed */
  top: calc(env(safe-area-inset-top, 0px) + 56px);
  left: env(safe-area-inset-left, 24px);
  z-index: 10;
  padding: 0.6rem 1.6rem;
  font-size: 1.1rem;
  border: none;
  border-radius: 5px; /* Less rounded, more boxy */
  background-color: rgba(0, 0, 0, 0.7);
  color: #fff;
  /* font-weight: 600; */ /* Remove or comment out this line */
  box-shadow: 0 2px 8px rgba(0,0,0,0.12);
  cursor: pointer;
  transition: background-color 0.2s, color 0.2s;
}

/* Lighter hover: white overlay with dark text */
.back-btn:hover {
  background-color: #fff;
  color: #222;
}
#counter { font-size: 4rem; }
#gender  { font-size: 6rem; }


.duration-input {
  margin-bottom: 1rem;
  font-size: 1.2rem;
}

.gender-selection {
  margin-bottom: 1rem;
}

.gender-btn, .start-btn {
  font-size: 1rem;
  padding: 0.5rem 1rem;
  margin: 0.2rem;
  cursor: pointer;
  border: none;
  border-radius: 8px;
  color: white;
}

.gender-btn {
  background-color: grey;
}

.start-btn {
  background-color: teal;
}
